<template>
  <div class="container py-5">
    <NavTab />
    <h1>Report</h1>

    <table class="table">
      <thead class="bg-warning">
        <tr>
          <th scope="col" width="60" class="text-right">#</th>
          <th scope="col">Task</th>
          <th scope="col" width="90">Period</th>
          <th scope="col" width="210">Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(task, index) in tasks" :key="task.id">
          <th scope="row" class="text-right">{{ index + 1 }}</th>
          <td class="position-relative">
            <div v-show="!task.isEditing" class="task-name">{{ task.name }}</div>
            <input v-show="task.isEditing" v-model="task.name" type="text" class="form-control">
            <span v-show="task.isEditing" class="cancel" @click.stop.prevent="cancelIsEditing(task._id)">x</span>
          </td>
          <td>{{ task.period }} mins</td>
          <td class="">
            <button v-show="!task.isEditing" class="btn btn-outline-primary" @click.stop.prevent="toggleIsEditing(task._id)">edit</button>
            <button v-show="task.isEditing" class="btn btn-outline-info ml-2" @click.stop.prevent="updateTask(task._id)">save</button>
            <button class="btn btn-outline-danger ml-2" @click.stop.prevent="deleteTask(task._id)">x</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import NavTab from '../components/NavTabs.vue'
import axios from 'axios'

export default {
  components: {
    NavTab
  },
  data () {
    return {
      id: -1,
      tasks: []
    }
  },
  created () {
    this.fetchTasks()
  },
  methods: {
    async fetchTasks () {
      try {
        const { data } = await axios({
          method: 'post',
          url: '/',
          data: {
            token: localStorage.getItem('token') || '0'
          }
        })
        this.tasks = data.tasks.map(task => ({
          ...task,
          isEditing: false,
          nameCached: ''
        }))
        console.log(this.tasks)
      } catch (error) {
        console.log('error', error)
      }
    },
    async deleteTask (id) {
      try {
        await axios({
          method: 'delete',
          url: `tasks/:id`,
          data: {id: id}
        })
        this.tasks = this.tasks.filter(task => task._id !== id)
      } catch (error) {
        console.log('error', error)
      }
    },
    async updateTask (taskId) {
      this.toggleIsEditing(taskId)
      const updatedTaskObject = this.tasks.filter(task => task._id === taskId)
      const updatedTask = updatedTaskObject[0].name
      await axios({
          method: 'put',
          url: `tasks/${taskId}`,
          data: {name: updatedTask}
        })
        .then(response => console.log(response))
    },
    async toggleIsEditing (taskId) {
      this.tasks = this.tasks.map(task => {
        if (task._id === taskId) {
          return {
            ...task,
            isEditing: !task.isEditing,
            nameCached: task.name
          }
        }
        return task
      })
    },
    async cancelIsEditing (taskId) {
      this.tasks = this.tasks.map(task => {
        if (task._id === taskId) {
          return {
            ...task,
            name: task.nameCached
          }
        }
        return task
      })
      this.toggleIsEditing(taskId)
    },
  }
}
</script>

<style scoped>
  .task-name {
    padding: 0.375rem 0.75rem;
    border: 1px solid transparent;
    outline: 0;
    cursor: auto;
  }
  .cancel {
    position: absolute;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    border: 1px solid #aaaaaa;
    border-radius: 20%;
    user-select: none;
    cursor: pointer;
    font-size: 15px;
    color: red;
    border-color: red;
  }
</style>